<template>
    <div>

        <loading :active.sync="isLoading"></loading>

        <div class="cartIcon d-flex justify-content-center align-items-center" @click="cartIsActive = !cartIsActive">
            <i class="fas fa-shopping-cart text-primary">
                <h6><span class="badge" v-if="carts.length > 0"> {{ carts.length }} </span></h6>
            </i>
        </div>

        <div class="cartRight" :class="{'cartRightShow' : cartIsActive}">
            <div class="cartArea d-flex justify-content-center">
                <div class="cartItem">
                    <div class="cartCloseBtn" @click.prevent="cartIsActive = !cartIsActive">
                        <i class="fas fa-arrow-circle-right"></i>
                    </div>
                    <table class="table table-hover mt-5">

                        <thead>
                            <tr>
                                <th></th>
                                <th class="cartImg"></th>
                                <th>品名</th>
                                <th>數量</th>
                                <th>金額</th>
                            </tr>
                        </thead>

                        <tbody>
                            <tr v-for="item in carts" :key="item.id">
                                <td class="p-3"> <a href="#" @click.prevent="delCart(item.id)"><i class="fas fa-trash h4"></i></a> </td>
                                <td class="cartImg"> <img :src="item.product.imageUrl" alt="" width="50px" height="50px"> </td>
                                <td> {{ item.product.title }} </td>
                                <td> {{ item.qty }} </td>
                                <td class="text-danger"> {{ item.total | dollarSign}} </td>
                            </tr>
                        </tbody>

                        <tfoot>
                            <tr>
                                <td colspan="4" class="text-right">總金額</td>
                                <td class="text-right text-danger h4"> {{ totalPrice | dollarSign }} </td>
                            </tr>
                        </tfoot>

                    </table>
                </div>
            </div>
        </div>

    </div>
</template>

<script>

import $ from 'jquery';

export default {
    data () {
        return {
            carts : [],
            totalPrice : '',
            cartIsActive : false,
            isLoading : false
        }
    },
    methods : {
        getCart(){
            const self = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMERPATH}/cart`;

            self.$http.get(api).then((res) => {
                self.isLoading = false;
                if(res.data.success){
                    self.carts = res.data.data.carts;
                    self.totalPrice = res.data.data.total;
                }
            })
        },
        delCart(id){
            const self = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMERPATH}/cart/${id}`;

            self.isLoading = true;

            self.$http.delete(api).then((res) => {
                
                self.getCart();
            })
        }
    },
    created(){
        const self = this;

        self.getCart();
        self.$bus.$on('addProduct' , () => {
            self.getCart();
        })
    }
}
</script>


